<template>
  <div class="login-wrap">
    <!-- 背景图 -->
    <div class="background-image"></div>
    <!-- 登录表单 -->
    <el-form  ref="form" :model="form" class="login-form">
      <h1 class="login-title">部门员工管理系统</h1>
      <el-form-item prop="username">
        <el-input v-model="form.username" prefix-icon="el-icon-user" placeholder="请输入用户名" @keydown.enter="submitForm"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input v-model="form.password" prefix-icon="el-icon-lock" type="password" placeholder="请输入密码" @keydown.enter="submitForm"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button class="login-btn" type="primary" @click="submitForm">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>    
import axios from 'axios';
import { Message } from 'element-ui';

export default {
  name: 'Login',
  data() {
    return {
      form: {
        username: '',
        password: ''
      }
    };
  },
  methods: {
    submitForm() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          axios.post('api/login', this.form)
            .then((result) => {
              const token = result.data.data;
              if (token) {
                localStorage.setItem('token', token);
                // 登录成功，跳转到新页面
                this.$router.push('/');
              } else {
                Message.error('用户名或密码错误！');
              }
            })
            .catch((error) => {
              console.error(error);
              Message.error('服务器错误，请稍后再试');
            });
        } else {
          console.log('登录表单验证失败');
          return false;
        }
      });
    },
    keydownListener(event) {
      if (event.keyCode === 13) {
        this.submitForm();
      }
    },
  },
   mounted() {
    document.addEventListener('keydown', this.keydownListener);
  },
  beforeDestroy() {
    document.removeEventListener('keydown', this.keydownListener);
  }
};
</script>

<style>
.login-wrap {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url('image/Talon.jpg');
  background-size: cover;
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.3;
  background-image: url('image/c.png');
  background-size: cover;
}

.login-form {
  width: 320px;
  padding: 20px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.login-title {
  font-size: 24px;
  text-align: center;
  margin-bottom: 20px;
}

.login-btn {
  width: 100%;
}
</style>
