<template>
  <div class="homeBox">
    <el-container style="height: 100%;">
      <el-header style=" border-bottom: 1px solid #eee;display: flex; align-items: center; height: 50px;">
        <el-button class="el-buttonOfHeader" style="width: 205px;height: 50px;text-align:center;border-right: none;margin-left: -21px;margin-top: 1px;">
            <img class = "image" src="https://ts1.cn.mm.bing.net/th?id=OIP-C.l7DqF8uxZM2mgyGWBDQIHAHaHa&w=250&h=250&c=8&rs=1&qlt=90&o=6&dpr=1.3&pid=3.1&rm=2">部门员工管理系统
        </el-button>
        <div style="display:flex; align-items: center;">
          <!-- 面包屑 -->
          <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-left: 20px;margin-top:10px;margin-bottom:10px;">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/dept' }">系统信息管理</el-breadcrumb-item>
            <el-breadcrumb-item>部门管理</el-breadcrumb-item>
          </el-breadcrumb>
         
        </div>
        <!-- <el-button class="el-buttonOfHeader" style="margin-left: auto; margin-right: 20px;">退出登录</el-button> -->
      </el-header>
      <el-container>
        <el-aside class="aside" width="204px">
            <el-menu 
                :default-active="$route.path" 
                class="el-menu-vertical-demo" 
                background-color="#3a4a6e "
                @select="handleSelect"
                text-color="#fff"
                active-text-color="#ffd04b"
                router
                style="border: 0px solid #3a4a6e">
                <el-menu-item index="/" >
                    <i class="el-icon-location"></i>
                    <span slot="title">首页</span>
                </el-menu-item>
                <el-submenu index="$route.path" >
                    <template slot="title">
                        <i class="el-icon-message"></i>
                        <span>系统信息管理</span>
                    </template>
                    <el-menu-item index="/dept">
                    <i class="el-icon-edit"></i>
                    部门管理
                    </el-menu-item>
                    <el-menu-item index="/emp">
                    <i class="el-icon-edit"></i>
                    员工管理
                    </el-menu-item>
                </el-submenu>
            </el-menu>
        </el-aside>
        <el-main style="padding:20px">
          <!-- 表格上方的操作按钮 -->
          <el-button
            type="primary"
            size="mini"
            icon="el-icon-circle-plus"
            @click="addDialogVisible = true">添加部门
          </el-button>
          <!-- 表格 -->
          <el-table :data="tableData" border>
            <el-table-column prop="id" label="序号" width="120"></el-table-column>
            <el-table-column prop="name" label="部门名称" width="300"></el-table-column>
            <el-table-column prop="updateTime" label="最后操作时间" width="350">
               <template slot-scope="scope">
                 {{ new Date(scope.row.updateTime).toLocaleString().replace(/\//g,'-') }}
               </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  size="small"
                  @click="editDialogVisible = true; beforeHandleEdit(scope.row)">编辑</el-button>
                <el-button
                  size="small"
                  type="danger"
                  @click="handleDelete(scope.row.id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <!-- 添加dialog -->
            <el-dialog title="添加部门" :visible.sync="addDialogVisible"
            :style="{ width: '55%', height: '98%', padding: '10px', margin: '6% auto'}" center>
            <el-form :model="addDialogForm" :rules="addDialogFormRules" ref="addDialogForm"
            label-position="right" label-width="80px">
              <el-form-item label="部门名称" prop="name">
                <el-input v-model="addDialogForm.name" autocomplete="off" placeholder="请输入部门名称"></el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="addDialogVisible = false">取 消</el-button>
              <el-button type="primary"  @click="handleAdd">确 定</el-button>
            </div>
          </el-dialog>
          <!-- 编辑dialog -->
            <el-dialog title="修改部门" :visible.sync="editDialogVisible"
            :style="{ width: '55%', height: '98%', padding: '10px', margin: '6% auto'}" center>
            <el-form :model="editDialogForm" :rules="addDialogFormRules" ref="editDialogForm"
            label-position="right" label-width="80px">
              <el-form-item label="部门名称"  prop="name">
                <el-input v-model="editDialogForm.name" autocomplete="off" placeholder="请输入部门名称"></el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="editDialogVisible = false">取 消</el-button>
              <el-button type="primary"  @click="handleEdit">确 定</el-button>
            </div>
          </el-dialog>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
            activeIndex : '/',
            tableData:[],
            addDialogVisible: false,
            editDialogVisible: false,
            addDialogFormRules: {
              name: [{ required: true, message: "请输入部门名称", trigger: "blur" }],
            },
            addDialogForm:{
              name: "",
            },
            editDialogForm:{
              id:"",
              name: "",
            },

        }
    },
    watch: {
        $route () {
            this.handleSelect(this.activeIndex);
        }
     },
    methods: {
    //添加员工
    async handleAdd(){
      if(this.addDialogForm.name===""){
        return this.$message.error("部门名称不能为空！");
      }
      const confirmR = await this.$confirm(
        "确认添加?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        }
      ).catch((err) => err);
      if (confirmR == "confirm"){
        //进行添加
        axios.
          post("/api/depts/",{
              name: this.addDialogForm.name,
          },
          {
          headers:{
          'Content-Type': 'application/json',
          'token': localStorage.getItem('token')
          },
          })
          .then(result => {
          console.log(result.data.code);
          if(result.data.code == 0){
            return this.$message.error("添加失败！该部门名已存在~");
          }
          this.$message.success("添加成功");
          this.addDialogVisible = false;
          this.addDialogForm={//清空对话框中的内容
                name: ""
          };
          this.fetchData();
        }).catch(error => {
          console.log(error);
          this.$message.error("添加失败");
        });
      }else{
        return this.$message.info("当前操作已取消");
      }  
    },
    //编辑部门
    async handleEdit(){
      if(this.editDialogForm.name===""){
        return this.$message.error("部门名称不能为空！");
      }
      const confirmR = await this.$confirm(
        "确认修改?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        }
      ).catch((err) => err);
      if (confirmR == "confirm"){
        //进行修改
        axios.
          put("/api/depts/",{
              name: this.editDialogForm.name,
              id:this.editDialogForm.id
          },
          {
          headers:{
          'Content-Type': 'application/json',
          'token': localStorage.getItem('token')
          },
        })
          .then(result => {
          console.log(result.data.code);
          if(result.data.code == 0){
            return this.$message.error("修改失败");
          }
          this.$message.success("修改成功");
          this.editDialogVisible = false;
          this.editDialogForm={//清空对话框中的内容
                name: "",
          };
          this.fetchData();
        }).catch(error => {
          console.log(error);
          this.$message.error("修改失败");
        });
      }else{
        return this.$message.info("当前操作已取消");
      }
    },
    //点击编辑部门按钮后,数据回显
    beforeHandleEdit(rows){
     this.editDialogForm = JSON.parse(JSON.stringify(rows));
     this.editDialogForm.id = rows.id;
    },
    //删除 
    async handleDelete(id) {
      // console.log("id:", id);
      const confirmR = await this.$confirm(
        "此操作将删除该数据, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        }
      ).catch((err) => err);
      if (confirmR == "confirm") {
        // 进行删除
        axios.
          delete("/api/depts/"+id,{
            headers: {
            'token': localStorage.getItem('token')
          }
          })
          .then(result => {
          console.log(result.data.code);
          if(result.data.code == 0){
            return this.$message.error("删除失败");
          }
          this.$message.success("删除成功");
          this.fetchData();
        })
      }  else {
        return this.$message.info("当前操作已取消");
      }
    },
      
        onSubmit() {
            console.log('submit!');
        },
        fetchData(){
          axios.get("api/depts",{
            headers:{
            'token': localStorage.getItem('token')
          },
          }).then((result) =>{
            this.tableData = result.data.data;
        });
        }

    },
    mounted() {
        //发送异步请求，获取数据
        this.fetchData();
    },
    created(){
      // 发送请求获取token，如果未登录，跳转到登录页面
      if(!localStorage.getItem('token')){
        this.$router.push('/login');
        //router.push('/login');
      }
    },
};
</script >

<style>
</style>