<template>
  <div class="homeBox">
    <el-container style="height: 100%;">
      <el-header style=" border-bottom: 1px solid #eee;display: flex; align-items: center; height: 50px;">
        <el-button class="el-buttonOfHeader" style="width: 205px;height: 50px;text-align:center;border-right: none;margin-left: -21px;margin-top: 1px;">
            <img class = "image" src="https://ts1.cn.mm.bing.net/th?id=OIP-C.l7DqF8uxZM2mgyGWBDQIHAHaHa&w=250&h=250&c=8&rs=1&qlt=90&o=6&dpr=1.3&pid=3.1&rm=2">部门员工管理系统
        </el-button>
        <div style="display:flex; align-items: center;">
          <!-- 面包屑 -->
          <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-left: 20px;margin-top:10px;margin-bottom:10px;">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/emp' }">系统信息管理</el-breadcrumb-item>
            <el-breadcrumb-item>员工管理</el-breadcrumb-item>
          </el-breadcrumb>
         
        </div>
        <!-- <el-button class="el-buttonOfHeader" style="margin-left: auto; margin-right: 20px;">退出登录</el-button> -->
      </el-header>
      <el-container>
        <el-aside class="aside" width="204px">
            <el-menu 
                :default-active="$route.path" 
                class="el-menu-vertical-demo" 
                background-color="#3a4a6e "
                @select="handleSelect"
                text-color="#fff"
                active-text-color="#ffd04b"
                router
                style="border: 0px solid #3a4a6e">
                <el-menu-item index="/" >
                    <i class="el-icon-location"></i>
                    <span slot="title">首页</span>
                </el-menu-item>
                <el-submenu index="$route.path" >
                    <template slot="title">
                        <i class="el-icon-message"></i>
                        <span>系统信息管理</span>
                    </template>
                    <el-menu-item index="/dept">
                    <i class="el-icon-edit"></i>
                    部门管理
                    </el-menu-item>
                    <el-menu-item index="/emp">
                    <i class="el-icon-edit"></i>
                    员工管理
                    </el-menu-item>
                </el-submenu>
            </el-menu>
        </el-aside>
        <el-main style="padding:20px">
          <!-- 表单 -->
          <el-form
            :inline="true"
            :model="formInline"
            class="demo-form-inline"
            ref="formInline"
            size="medium"
          >
            <el-form-item label="姓名" prop="name">
              <el-input v-model="formInline.name" placeholder="姓名" ></el-input>
            </el-form-item>
            <el-form-item label="性别" prop="gender">
              <el-select v-model="formInline.gender" placeholder="性别">
                <el-option label="男" value="1"></el-option>
                <el-option label="女" value="2"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="入职日期" prop="entrydate">
              <el-date-picker
                v-model="formInline.entrydate"
                type="daterange"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                value-format="yyyy-MM-dd"
              ></el-date-picker>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="select">查询</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="resetForm('formInline')">重置</el-button>
            </el-form-item>
          </el-form>
          <!-- 表格上方的按钮 -->
           <div style="display: flex; justify-content:flex-start">
          <el-button
            type="danger"
            icon="el-icon-remove"
            size="small"
            @click="alldelete"
            :disabled="multipleSelection.length === 0">批量删除 </el-button>
       
           <el-button
            type="primary"
            size="mini"
            icon="el-icon-circle-plus"
            @click="addDialogVisible = true">添加员工</el-button>
          </div>
          <br>
          <!-- 表格 -->
          <el-table :data="tableData" border 
          :header-cell-style="{ background: '#F2F6FC', color: '#606266' }"
          ref="multipleTable"
          size="medium"
          @selection-change="handleSelectionChange">
          
            <el-table-column type="selection" width="60" align="center"></el-table-column>
            <el-table-column prop="name" label="姓名" width="150" align="center"></el-table-column>
            <el-table-column prop="image" label="图像" width="150" align="center">
              <template slot-scope="scope">
                <img :src="scope.row.image" width="75px" height="60px" />
              </template>
            </el-table-column>
            <el-table-column prop="gender" label="性别" width="150" align="center">
              <template slot-scope="scope">
                {{ scope.row.gender == 1 ? "男" : "女" }}
              </template>
            </el-table-column>
            <el-table-column prop="job" label="职位" width="150" align="center">
             <template slot-scope="scope">
                  <span v-if="scope.row.job == 1">上单</span>
                  <span v-else-if="scope.row.job == 2">打野</span>
                  <span v-else-if="scope.row.job == 3">中单</span>
                  <span v-else-if="scope.row.job == 4">ADC</span>
                  <span v-else>辅助</span>
              </template>
            </el-table-column >
            <el-table-column prop="entrydate" label="入职日期" width="200" align="center"></el-table-column>
            <el-table-column prop="updateTime" label="最后操作时间" width="200" align="center">
               <template slot-scope="scope">
                {{ new Date(scope.row.updateTime).toLocaleString().replace(/\//g,'-') }}
              </template>
            </el-table-column>
            <el-table-column label="操作" align="center">
               <template slot-scope="scope">
                <el-button
                  type="primary"
                  size="small"
                  @click="editDialogVisible = true; beforeHandleEdit(scope.row)">编辑</el-button>
                <el-button
                  size="small"
                  type="danger"
                  @click="handleDelete( scope.row.id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>

          <!-- 添加员工dialog -->
         <el-dialog class="dialogClass" title="添加员工" :visible.sync="addDialogVisible"  
          :style="{ width: '55%', height: '98%', padding: '10px', margin: '-3% auto'}" center :before-close="beforeCloseDIalog">
            <el-form :model="addDialogForm" :rules="addDialogFormRules" ref="addDialogForm" 
              label-position="right" label-width="80px" size="mini">
              <el-form-item label="用户名" prop="username" >
                <el-input v-model="addDialogForm.username" autocomplete="off" placeholder="请输入用户名"></el-input>
              </el-form-item>
              <el-form-item label="员工姓名" prop="name" >
                <el-input v-model="addDialogForm.name" autocomplete="off" placeholder="请输入员工姓名"></el-input>
              </el-form-item>
              <el-form-item label="性别"  prop="gender" >
                <el-select v-model="addDialogForm.gender" placeholder="请选择性别">
                  <el-option label="男" value="1"></el-option>
                  <el-option label="女" value="2"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="头像" prop="image">
                <el-upload
                v-model="addDialogForm.image" placeholder="请选择图片"
                  class="avatar-uploader"
                  action="https://jsonplaceholder.typicode.com/posts/"
                  :show-file-list="false"
                  :before-upload="beforeAvatarUpload">
                  <img v-if="addDialogForm.image" :src="addDialogForm.image" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="职位"  prop="job" >
                <el-select v-model="addDialogForm.job" placeholder="请选择职位">
                  <el-option label="上单" value="1"></el-option>
                  <el-option label="打野" value="2"></el-option>
                  <el-option label="中单" value="3"></el-option>
                  <el-option label="ADC"  value="4"></el-option>
                  <el-option label="辅助" value="5"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="归属部门" prop="deptId" >
                <el-select v-model="addDialogForm.deptId" placeholder="请选择部门">
                  <el-option label="坦克" value="1"></el-option>
                  <el-option label="战士" value="2"></el-option>
                  <el-option label="刺客" value="3"></el-option>
                  <el-option label="法师" value="4"></el-option>
                  <el-option label="射手" value="5"></el-option>
                  <el-option label="辅助" value="6"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="入职日期" prop="entrydate" >
                <el-date-picker v-model="addDialogForm.entrydate" type="date" placeholder="选择日期"></el-date-picker>
              </el-form-item>           
              <el-form-item>
                <el-button @click="resetDialogForm('addDialogForm')">重 置</el-button>
                <el-button type="primary" @click="handleAdd">提 交</el-button>
              </el-form-item>
            </el-form>
          </el-dialog>
          <!-- 编辑员工dialog -->
         <el-dialog class="dialogClass" title="编辑员工" :visible.sync="editDialogVisible"  
          :style="{ width: '55%', height: '98%', padding: '10px', margin: '-3% auto'}" center :before-close="beforeCloseDIalog">
            <el-form :model="editDialogForm" :rules="addDialogFormRules" ref="editDialogForm" 
              label-position="right" label-width="80px" size="mini">
              <el-form-item label="用户名" prop="username" >
                <el-input v-model="editDialogForm.username" autocomplete="off" placeholder="请输入用户名"></el-input>
              </el-form-item>
              <el-form-item label="员工姓名" prop="name" >
                <el-input v-model="editDialogForm.name" autocomplete="off" placeholder="请输入员工姓名"></el-input>
              </el-form-item>
              <el-form-item label="性别"  prop="gender" >
                <el-select v-model="editDialogForm.gender" placeholder="请选择性别">
                  <el-option label="男" :value="1"></el-option>
                  <el-option label="女" :value="2"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="头像" prop="image">
                <el-upload
                v-model="editDialogForm.image" placeholder="请选择图片"
                  class="avatar-uploader"
                  action="https://jsonplaceholder.typicode.com/posts/"
                  :show-file-list="false"
                  :before-upload="beforeAvatarUpload">
                  <img v-if="editDialogForm.image" :src="editDialogForm.image" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="职位" prop="job">

                <el-select v-model="editDialogForm.job" placeholder="请选择职位">
                  <el-option label="上单" :value="1"></el-option>
                  <el-option label="打野" :value="2"></el-option>
                  <el-option label="中单" :value="3"></el-option>
                  <el-option label="ADC"  :value="4"></el-option>
                  <el-option label="辅助" :value="5"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="归属部门" prop="deptId" >
                <el-select v-model="editDialogForm.deptId" placeholder="请选择部门">
                  <el-option label="坦克" :value="1"></el-option>
                  <el-option label="战士" :value="2"></el-option>
                  <el-option label="刺客" :value="3"></el-option>
                  <el-option label="法师" :value="4"></el-option>
                  <el-option label="射手" :value="5"></el-option>
                  <el-option label="辅助" :value="6"></el-option>
                </el-select>
                <!-- <el-select v-model="editDialogForm.deptId" placeholder="请选择部门">
                <el-option
                  v-for="dept in depts"
                  :key="dept.id"
                  :label="dept.name"
                  :value="dept.id"
                ></el-option>
              </el-select> -->
              </el-form-item>
              <el-form-item label="入职日期" prop="entrydate" >
                <el-date-picker v-model="editDialogForm.entrydate" type="date" placeholder="选择日期"></el-date-picker>
              </el-form-item>           
              <el-form-item>
                <el-button @click="editDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="handleEdit">提 交</el-button>
              </el-form-item>
            </el-form>
          </el-dialog>
          <br />
          <!-- 分页条 -->
          <el-pagination
            background
            layout="total,sizes,prev, pager, next,jumper"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :total="total"
            :page-sizes="[5, 10, 20, 50, 100]"
            :page-size="pageSize"
            :current-page="page"
          ></el-pagination>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
import OSS from 'ali-oss/dist/aliyun-oss-sdk.min.js';
const client = new OSS({
  region: 'oss-cn-beijing',
  accessKeyId: 'LTAI5t7JXcd2N4eNBtLrmpak',
  accessKeySecret: 'BPdEnrpOdd40gjwgvGuJvUfWCsGuSD',
  bucket: 'java-web-study',
});

export default {
  data() {
    return {
      activeIndex : '/',
      tableData: [],
      formInline: {
        name: "",
        gender: "",
        entrydate: "",
        
      },
       jobOptions: [
        { value: '1', label: '上单' },
        { value: '2', label: '打野' },
        { value: '3', label: '中单' },
        { value: '4', label: 'ADC' },
        { value: '5', label: '辅助' },
     ],
      depts: [],
      addDialogVisible: false,
      editDialogVisible: false,
      addDialogFormRules: {
        username: [{ required: true, message: "请输入用户名", trigger: "blur" }],
        name: [{ required: true, message: "请输入姓名", trigger: "blur" }],
        gender: [{ required: true, message: "请选择性别", trigger: "change" }],
        job: [{ required: true, message: "请输入职位", trigger: "blur" }],
        deptId: [{ required: true, message: "请输入部门", trigger: "blur" }],
        entrydate: [{ required: true, message: "请选择入职日期", trigger: "change" }]
      },
      addDialogForm:{
        username: "",
        name: "",
        gender: "",
        image: "",
        job: "",
        deptId: "",
        entrydate: "",
      },
      editDialogForm:{
        id:"",
        username: "",
        name: "",
        gender: "",
        image: "",
        job: "",
        deptId: "",
        entrydate: "",
      },
      begin:"",
      end:"",
      total: 10,
      pageSize: 5,
      page: 1,
      multipleSelection: [], //选中行数据
    };
  },
  watch: {
        $route () {
            this.handleSelect(this.activeIndex);
        }
     },
  methods: {
    //多选
     handleSelect (index) {
            this.activeIndex = index;
        },
    //查询
    select() {
      this.fetchData();
    },
    //重置
    resetForm(formName) {
        this.$refs[formName].resetFields();
        this.fetchData();
      },
    //页数大小改变
    handleSizeChange(val) {
      this.pageSize = val;
      this.fetchData();
    },
    //当前页码变化
    handleCurrentChange(val) {
      this.page = val;
      this.fetchData();
    },
    //重置dialogForm
    resetDialogForm(formName) {
        this.$refs[formName].resetFields();
      },
    //关闭dialog之前
    beforeCloseDialog(done) {
      
      this.$refs.addDialogForm.validate(valid => {
        if (valid) {
          done();
        } else {
          return false;
        }
      });
    },
    //在选择文件上传前
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isPNG = file.type === "image/png";
      const isLt5M = file.size / 1024 / 1024 < 5;

      if (!isJPG && !isPNG) {
        this.$message.error("上传头像图片只能是 JPG/PNG 格式!");
        return false;
      }
      if (!isLt5M) {
        this.$message.error("上传头像图片大小不能超过 5MB!");
        return false;
      }

      const reader = new FileReader();
      reader.readAsArrayBuffer(file);
      reader.onload = event => {
        const imgData = event.target.result;
        const filename = file.name;
        const blob = new Blob([imgData]);
        client.put(filename, blob).then(res => {
          console.log("上传头像成功", res.url);
          this.addDialogForm.image = res.url;
          this.editDialogForm.image = res.url;
        });
};

      return true;
    },
    //添加员工
    async handleAdd(){
      const confirmR = await this.$confirm(
        "确认添加?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        }
      ).catch((err) => err);
      if (confirmR == "confirm"){
        //进行添加
        axios.
          post("/api/emps/",{
               username: this.addDialogForm.username,
              name: this.addDialogForm.name,
              gender: this.addDialogForm.gender,
              image: this.addDialogForm.image,
              job: this.addDialogForm.job,
              deptId: this.addDialogForm.deptId,
              entrydate: this.addDialogForm.entrydate
            },
            {
             headers: {
            'token': localStorage.getItem('token')
          }
          })
          .then(result => {
          console.log(result.data.code);
          if(result.data.code == 0){
            return this.$message.error("添加失败！该用户名已存在~");
          }
          this.$message.success("添加成功");
          this.addDialogVisible = false;
          this.addDialogForm={//清空对话框中的内容
            username: "",
                name: "",
                gender: "",
                image: "",
                job: "",
                deptId: "",
                entrydate: ""

          };
          this.fetchData();
        }).catch(error => {
          console.log(error);
          this.$message.error("添加失败");
        });
      }else{
        return this.$message.info("当前操作已取消");
      }  
    },
    //编辑员工
    async handleEdit(){
      const confirmR = await this.$confirm(
        "确认修改?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        }
      ).catch((err) => err);
      if (confirmR == "confirm"){
        //进行修改
        axios.
          put("/api/emps/",{
               id: this.editDialogForm.id,
              username: this.editDialogForm.username,
              name: this.editDialogForm.name,
              gender: this.editDialogForm.gender,
              image: this.editDialogForm.image,
              job: this.editDialogForm.job,
              deptId: this.editDialogForm.deptId,
              entrydate: this.editDialogForm.entrydate
            },
            {
          headers: {
            'token': localStorage.getItem('token')
          }
          })
          .then(result => {
          console.log(result.data.code);
          if(result.data.code == 0){
            return this.$message.error("修改失败");
          }
          this.$message.success("修改成功");
          this.editDialogVisible = false;
          this.editDialogForm={//清空对话框中的内容
            username: "",
                name: "",
                gender: "",
                image: "",
                job: "",
                dept: "",
                entrydate: ""

          };
          this.fetchData();
        }).catch(error => {
          console.log(error);
          this.$message.error("修改失败");
        });
      }else{
        return this.$message.info("当前操作已取消");
      }
    },
    //点击编辑员工按钮后,数据回显
    beforeHandleEdit(rows){
     //这里不能这样写，如果这样写，会赋值地址，即使编辑未提交，表格渲染也会跟着改变 this.editDialogForm = rows;
     //所以要进行深拷贝，利用json就可以了
     this.editDialogForm = JSON.parse(JSON.stringify(rows));
     this.editDialogForm.id = rows.id;
    },
    //获取选中行信息
    handleSelectionChange(val) {
      // console.log(val);
      this.multipleSelection = [];
      for (let selectedItem of val) {
        this.multipleSelection.push(selectedItem.id);
      }
      // console.log(this.multipleSelection);
    },
    //单行删除 
    async handleDelete(id) {
      // console.log("id:", id);
      const confirmR = await this.$confirm(
        "此操作将删除该数据, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        }
      ).catch((err) => err);
      if (confirmR == "confirm") {
        // 进行删除
        axios.
          delete("/api/emps/"+id,{
            headers: {
            'token': localStorage.getItem('token')
          }
          })
          .then(result => {
          console.log(result.data.code);
          if(result.data.code == 0){
            return this.$message.error("删除失败");
          }
          this.$message.success("删除成功");
          this.fetchData();
        })
      }  else {
        return this.$message.info("当前操作已取消");
      }
    },
    //批量删除
    async alldelete() {
      // console.log(this.multipleSelection);
      const confirmR = await this.$confirm(
        "此操作将删除该数据, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true,
        }
      ).catch((err) => err);
      if (confirmR == "confirm") {
        // 进行删除
        axios.
          delete("/api/emps/"+this.multipleSelection,{
             headers: {
            'token': localStorage.getItem('token')
          }
          })
          .then(result => {
          console.log(result.data.code);
          if(result.data.code == 0){
            return this.$message.error("删除失败");
          }
          this.$message.success("删除成功");
          this.fetchData();
        })
      }  else {
        return this.$message.info("当前操作已取消");
      }
    },
    fetchData() {
      axios
        .get("/api/emps", {
          params: {
            name: this.formInline.name,
            gender: this.formInline.gender,
            begin: this.formInline.entrydate[0],
            end: this.formInline.entrydate[1],
            pageSize: this.pageSize,
            page: this.page,
          },
          headers:{
          'token': localStorage.getItem('token')
          },
        })
         .then(result => {
          console.log(result.data.data);
          console.log(this.formInline.entrydate[0]);
          console.log(this.formInline.entrydate[1]);
          this.tableData = result.data.data.rows;
          this.total = result.data.data.total;
        })
    },
  },
  mounted() {
    //发送异步请求，获取数据
    this.fetchData();
    
  },
  created(){
      // 发送请求获取token，如果未登录，跳转到登录页面
      if(!localStorage.getItem('token')){
        this.$router.push('/login');
      }
    },
  // created(){
  //   this.$bus.$on('updateDepts', (depts) => {
  //   this.depts = depts;
  //   console.log(depts);
  // });
  // }
};
</script>

